const Birthday = ({name,img,age}) => {
  return ( 
          <article>
            <div className="img-container">
              <img src={img} alt={img}/>
            </div>
            <div className="info">
              <h3>{name}</h3>
              <p>{age}</p>
            </div>
          </article>
        )
}
 
export default Birthday;